import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Card, Row, Col, Divider } from 'antd';
import { BtnClear, BtnSave, BtnSearch } from 'components/common/button';
import FormInfoStudentForCert from './FormInfoStudentForCert';
import TableCertificateSelected from './TableCertificateSelected';
import TableCertificateNotSelected from './TableCertificateNotSelected';
import MenuTabTop from './MenuTabTop';
import './style.scss';

class ReviewForCertificationPage extends Component {
  state = {};
  render() {
    return (
      <Card title="Lp m sinh vin">
        <Row>
          <Col span={24}>
            <MenuTabTop />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <FormInfoStudentForCert />
          </Col>
        </Row>
        <Divider>Danh sch sinh vin cha chn</Divider>
        <Row>
          <Col span={24} style={{ textAlign: 'right' }}>
            <div className="btn-group">
              <BtnSearch />
              <BtnClear />
            </div>
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <TableCertificateNotSelected />
          </Col>
        </Row>
        <Divider>Danh sch sinh vin  chn</Divider>
        <Row>
          <Col span={24} style={{ textAlign: 'right' }}>
            <div className="btn-group">
              <BtnSave />
            </div>
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <TableCertificateSelected />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <ReviewForCertificationPage />
  </Page>
);



