import React, { Component } from 'react';
import { Table, Button } from 'antd';
import { setStudentReviewCertificate } from 'dump/data';

const columns = [
  {
    key: 'operation',
    title: 'Thao tc',
    dataIndex: 'operation',
    render: () => (<Button type="primary">Xa</Button>)
  },
  {
    title: 'M sinh vin',
    dataIndex: 'code',
    key: 'code',
  },
  {
    title: 'H v tn',
    dataIndex: 'name',
    key: 'name',
  },
  {
    title: 'Ngy sinh',
    dataIndex: 'birthdate',
    key: 'birthdate',
  },
  {
    title: 'Gii tnh',
    dataIndex: 'sex',
    key: 'sex',
  },
  {
    title: 'Tn lp',
    dataIndex: 'class',
    key: 'class',
  },
  {
    title: 'TBC chng ch',
    dataIndex: 'TBCCertificate',
    key: 'TBCCC',
  },
  {
    title: 'Xp loi',
    dataIndex: 'ratingCertificate',
    key: 'ratingCertificate',
  },
  {
    title: 'Ln xt',
    dataIndex: 'timesReview',
    key: 'timesReview',
  }
];

class TableCertificateNotChecked extends Component {
  state = {};
  render() {
    return (
      <Table bordered dataSource={setStudentReviewCertificate} columns={columns} />
    );
  }
}

export default TableCertificateNotChecked;



